export default {
	onUnload() {
		console.log('导航页面卸载了')
	},
	
	methods: {
		goDetail () {
			uni.navigateTo({
				url: '/pages/detail/detail?id=80&age=19'
			})
		},
		goMessage () {
			uni.switchTab({
				url: '/pages/message/message'
			})
		},
		redirectDetail () {
			uni.redirectTo({
				url: '/pages/detail/detail'
			});
		},
		addToCar () {
			uni.$emit('updateCart',{
				id: 10,
				name: '贸易'
			})
		}
	}
}
